import {
	ADD_ITEM,
	REMOVE_ITEM,
	INC_QTY,
	DEC_QTY,
	CHECK_PRODUCT_IN_CART,
} from '../Actions/types';

const getCartProducts = () => {
	const products = JSON.parse(localStorage.getItem('cartPrd'));
	return products ? products : [];
};

const initialState = {
	products: getCartProducts(),
	noOfItems: getCartProducts().length,
	totalPrice: getTotalPrice(getCartProducts()),
	inCart: false,
};

function getTotalPrice(products) {
	const prices = products.map((product) => product.totalPrice);
	const totalPrice = prices.reduce((total, curr) => total + curr, 0);
	return totalPrice;
}

const saveCart = (products) =>
	localStorage.setItem('cartPrd', JSON.stringify(products));

const getProduct = (id, products) => {
	const product = products.find((product) => product._id === id);
	return products.indexOf(product);
};

const updateProduct = (id, products, op) => {
	const updProducts = products.map((product) => {
		if (id === product._id) {
			const qty =
				op === 'inc'
					? product.qty + 1
					: product.qty > 1
					? product.qty - 1
					: product.qty;
			const totalPrice = product.price * qty;
			return {
				...product,
				qty,
				totalPrice,
			};
		}
		return product;
	});
	saveCart(updProducts);
	return updProducts;
};

function cartReducer(state = initialState, action) {
	switch (action.type) {
		case ADD_ITEM: {
			const products = [
				...state.products,
				{ ...action.payload, qty: 1, totalPrice: action.payload.price },
			];
			saveCart(products);
			return {
				...state,
				products,
				noOfItems: products.length,
				totalPrice: getTotalPrice(products),
				inCart: true,
			};
		}
		case REMOVE_ITEM: {
			const products = [...state.products];
			const pos = getProduct(action.payload, products);
			products.splice(pos, 1);
			saveCart(products);
			return {
				...state,
				products,
				noOfItems: products.length,
				totalPrice: getTotalPrice(products),
			};
		}

		case INC_QTY: {
			const products = [...state.products];
			const updProducts = updateProduct(action.payload, products, 'inc');
			return {
				...state,
				products: updProducts,
				totalPrice: getTotalPrice(updProducts),
			};
		}

		case DEC_QTY: {
			const products = [...state.products];
			const updProducts = updateProduct(action.payload, products, 'dec');
			return {
				...state,
				products: updProducts,
				totalPrice: getTotalPrice(updProducts),
			};
		}
		case CHECK_PRODUCT_IN_CART: {
			const product = state.products.find(
				(product) => product._id === action.payload
			);
			return {
				...state,
				inCart: product ? true : false,
			};
		}

		default:
			return state;
	}
}

export default cartReducer;
